import Blockly from "blockly";
export { BlocklyToolkitView } from "../../blockly-toolkit";
window.userButtonInput = "";
const comImg = document.getElementById("comImg");
const userImg = document.getElementById("userImg");
const button1 = document.getElementById("button1");
const button2 = document.getElementById("button2");
const button3 = document.getElementById("button3");
const textdiv = document.getElementById("text");
button1.addEventListener("click", () => {
    window.userButtonInput = "GU";
});
button2.addEventListener("click", () => {
    window.userButtonInput = "CHOKI";
});
button3.addEventListener("click", () => {
    window.userButtonInput = "PA";
});
export const CustomBlock = [
    {
        name: "janken_rand",
        init() {
            this.appendDummyInput()
                .appendField("");
            this.setOutput(true, "String");
            this.setStyle('text_blocks');
            this.setTooltip("");
            this.setHelpUrl("");
        },
        codeGenerator(block, generator) {
            console.log("aaa");
            return [`(function(){
            switch (Math.floor(Math.random() * 3)) {
                case 0:
                    return "GU";
                case 1:
                    return "CHOKI";
                case 2:
                    return "PA";
                default:
                    throw "Other";
            }
        })()`, generator.ORDER_ADDITION];
        },
        interpreterInitFunc(interpreter, globalObject) {
        },
    },
    {
        name: "janken_user",
        init() {
            this.appendDummyInput()
                .appendField("");
            this.setOutput(true, "String");
            this.setStyle('text_blocks');
            this.setTooltip("");
            this.setHelpUrl("");
        },
        codeGenerator(block, generator) {
            return [`(function(){
            while (true) {
                window.sleepFunc();
                var userButtonInputAAA = window.getUserButtonInput();
                if (userButtonInputAAA) return userButtonInputAAA;
            }
        })()`, generator.ORDER_ADDITION];
        },
        interpreterInitFunc(interpreter, globalObject, context) {
            interpreter.setProperty(globalObject, "sleepFunc", interpreter.createNativeFunction(() => {
                context.sleepTime = 100;
            }));
            interpreter.setProperty(globalObject, "getUserButtonInput", interpreter.createNativeFunction(() => {
                const tmp = window.userButtonInput;
                window.userButtonInput = "";
                return tmp;
            }));
        },
    },
    {
        name: "janken_if",
        init() {
            this.appendDummyInput()
                .appendField("")
                .appendField(new Blockly.FieldVariable(null), "VAR")
                .appendField("");
            this.appendStatementInput("IFGU")
                .setCheck(null)
                .appendField("");
            this.appendStatementInput("IFCHOKI")
                .setCheck(null)
                .appendField("");
            this.appendStatementInput("IFPA")
                .setCheck(null)
                .appendField("");
            this.setInputsInline(true);
            this.setPreviousStatement(true, null);
            this.setNextStatement(true, null);
            this.setStyle('logic_blocks');
            this.setTooltip("");
            this.setHelpUrl("");
        },
        codeGenerator(block, generator) {
            var variable_var = generator.nameDB_.getName(block.getFieldValue('VAR'), Blockly.Names.NameType.VARIABLE);
            var statements_ifgu = generator.statementToCode(block, 'IFGU');
            var statements_ifchoki = generator.statementToCode(block, 'IFCHOKI');
            var statements_ifpa = generator.statementToCode(block, 'IFPA');
            return `;if (${variable_var} === "GU") {
                    ${statements_ifgu}
                } else if (${variable_var} === "CHOKI") {
                    ${statements_ifchoki}
                } else {
                    ${statements_ifpa}
                };`;
        },
        interpreterInitFunc(interpreter, globalObject, context) {
        },
    },
    {
        name: "janken_show_com",
        init() {
            this.appendValueInput("VAL")
                .setCheck("String")
                .appendField("");
            this.appendDummyInput()
                .appendField("");
            this.setInputsInline(true);
            this.setPreviousStatement(true, null);
            this.setNextStatement(true, null);
            this.setStyle('text_blocks');
            this.setTooltip("");
            this.setHelpUrl("");
        },
        codeGenerator(block, generator) {
            var value_val = generator.valueToCode(block, 'VAL', generator.ORDER_ATOMIC);
            return `window.setComImgSrc(${value_val});`;
        },
        interpreterInitFunc(interpreter, globalObject, context) {
            interpreter.setProperty(globalObject, "setComImgSrc", interpreter.createNativeFunction((val) => {
                comImg.src = "./" + val + ".svg";
            }));
        },
    },
    {
        name: "janken_show_user",
        init() {
            this.appendValueInput("VAL")
                .setCheck("String")
                .appendField("");
            this.appendDummyInput()
                .appendField("");
            this.setInputsInline(true);
            this.setPreviousStatement(true, null);
            this.setNextStatement(true, null);
            this.setStyle('text_blocks');
            this.setTooltip("");
            this.setHelpUrl("");
        },
        codeGenerator(block, generator) {
            var value_val = generator.valueToCode(block, 'VAL', generator.ORDER_ATOMIC);
            return `window.setUserImgSrc(${value_val});`;
        },
        interpreterInitFunc(interpreter, globalObject, context) {
            interpreter.setProperty(globalObject, "setUserImgSrc", interpreter.createNativeFunction((val) => {
                userImg.src = "./" + val + ".svg";
            }));
        },
    },
    {
        name: "janken_show_text",
        init() {
            this.appendValueInput("VAL")
                .setCheck("String")
                .appendField("");
            this.appendDummyInput()
                .appendField("");
            this.setInputsInline(true);
            this.setPreviousStatement(true, null);
            this.setNextStatement(true, null);
            this.setStyle('text_blocks');
            this.setTooltip("");
            this.setHelpUrl("");
        },
        codeGenerator(block, generator) {
            var value_val = generator.valueToCode(block, 'VAL', generator.ORDER_ATOMIC);
            return `window.showText(${value_val});window.sleepFunc2();window.showText(null);`;
        },
        interpreterInitFunc(interpreter, globalObject, context) {
            interpreter.setProperty(globalObject, "showText", interpreter.createNativeFunction((val) => {
                textdiv.textContent = val;
            }));
            interpreter.setProperty(globalObject, "sleepFunc2", interpreter.createNativeFunction(() => {
                context.sleepTime = 2000;
            }));
        },
    },
];
