import { BlocklyToolkit } from "./core";
import { Dialog } from "./dialog";
import { UDFontTheme } from "./theme";
import { CE } from "./util";
export class BlocklyToolkitView {
    constructor(el, toolboxJson, setting, customBlock) {
        let outputData = "";
        const blocklyDiv = CE("div", {
            "class": "BlocklyToolkitViewBlockly",
        });
        const output = CE("textarea", {
            "class": "BlocklyToolkitViewSideOutput",
            "readonly": "readonly",
        });
        el.appendChild(CE("div", {
            "class": "BlocklyToolkitView",
        }, [
            blocklyDiv,
            CE("div", {
                "class": "BlocklyToolkitViewSide",
            }, [
                CE("div", {
                    "class": "BlocklyToolkitViewSideSpeed",
                }, [
                    CE("label", {}, [""]),
                    CE("input", {
                        "class": "BlocklyToolkitViewSideSpeedInput",
                        "type": "range",
                        "min": "10",
                        "max": "1000",
                        "value": "500",
                    }, [], {
                        change: function (e) {
                            toolkit.setSleepTime(this.valueAsNumber);
                        }
                    }),
                ]),
                CE("div", {
                    "class": "BlocklyToolkitViewSideToolbar",
                }, [
                    CE("button", {
                        "class": "BlocklyToolkitViewSideToolbarButton",
                        "type": "button",
                    }, [""], {
                        click: (e) => {
                            toolkit.run();
                        }
                    }),
                    CE("button", {
                        "class": "BlocklyToolkitViewSideToolbarButton",
                        "type": "button",
                    }, [""], {
                        click: (e) => {
                            toolkit.stop();
                        }
                    }),
                ]),
                output,
                CE("div", {
                    "class": "BlocklyToolkitViewSideToolbar",
                }, [
                    CE("button", {
                        "class": "BlocklyToolkitViewSideToolbarButton",
                        "type": "button",
                    }, [""], {
                        click: (e) => {
                            toolkit.save();
                        }
                    }),
                    CE("button", {
                        "class": "BlocklyToolkitViewSideToolbarButton",
                        "type": "button",
                    }, [""], {
                        click: (e) => {
                            toolkit.load();
                        }
                    }),
                    ((setting === null || setting === void 0 ? void 0 : setting.OutputJavaScript) && CE("button", {
                        "class": "BlocklyToolkitViewSideToolbarButton",
                        "type": "button",
                    }, [""], {
                        click: (e) => {
                            const code = toolkit.getCode("");
                            Dialog(code);
                        }
                    }))
                ]),
            ])
        ]));
        const toolkit = new BlocklyToolkit(blocklyDiv, toolboxJson, UDFontTheme, customBlock);
        toolkit.addEventListener("console", (log) => {
            outputData += log + "\n";
            output.value = outputData;
        });
        toolkit.addEventListener("changestate", (state) => {
            if (state === "runnning") {
                outputData = "";
                output.value = outputData;
            }
        });
    }
}
